import { Component, Input } from '@angular/core';
import { PaymentTypeConfig } from '../../../configs/models/form-items.model';
import { getDateOnly, getRelativeDate } from '../../helpers/date-helpers';
import { ScheduleItem } from '../schedule/schedule.component';

@Component({
  selector: 'bb-payord-schedule-details',
  templateUrl: './schedule-details.component.html',
})
export class PayordScheduleDetailsComponent {
  /**
   * Payment schedule details
   */
  @Input() data!: ScheduleItem;

  /**
   * The payment config item
   */
  @Input() paymentConfig?: PaymentTypeConfig;

  isRelativeDate(date: string): string {
    const today = getRelativeDate();
    const tomorrow = getRelativeDate(1);
    const _date = getDateOnly(new Date(date));
    let relativeDate = '';

    if (_date === today) {
      relativeDate = 'today';
    } else if (_date === tomorrow) {
      relativeDate = 'tomorrow';
    }

    return relativeDate;
  }
}
